import { useState } from "react";
import { useAppActions, useAppState } from '../context'

export const useOverlayManager = () => {
 

  
  const { setOverlays } = useAppActions()
  const { setOverlayId } = useAppActions()

  
  const { overlays } = useAppState()
  const { OverlayId } = useAppState()

    const addOverlay = (overlay) => { setOverlays([...overlays, overlay]); } ;
    
    const deleteOverlay = (id) => {
      setOverlays(id);

    }

    return { overlays, addOverlay, deleteOverlay };
}


