import React, { useState , useEffect} from 'react';
import { FaBell } from "react-icons/fa";
import { FaRegCircle } from "react-icons/fa";
import { FaCamera } from "react-icons/fa";
import { FaSyncAlt } from "react-icons/fa";
import { FaEdgeLegacy } from "react-icons/fa";
import {myFunc} from './Home.js'
import { useAppActions, useAppState } from '../context'
import * as Mui from "@material-ui/core";

import { AddNotify } from './AddNotify';

import {useOverlayManager} from './useOverlayManager';
import OverlayIItem from './OverlayIItem';



const Notify = () => {

    const { addOverlay, deleteOverlay, overlays } = useOverlayManager();
    const { showUI } = useAppActions()

  useEffect(() => {
    function handleKeyDown(e) {
        if (e.keyCode == 27) { 
           
            showUI(false)
            
            fetch("https://ethicalpixel-adminmenu/close", {
              method: 'POST',
            })

         
          
        }
       
    }

    document.addEventListener('keydown', handleKeyDown);

    return function cleanup() {
      document.removeEventListener('keydown', handleKeyDown);
    }
}, []);

    // const { NotifySmall } = useAppActions()
    // const { setNotifications } = useAppActions()

    // const { Notifies } = AddNotify();
   

    return(
    <div className="top-notifications-wrapper">
       
    {
          overlays.map((overlay) => {
            return (
                    
                <OverlayIItem
                deleteOverlay={deleteOverlay}
                overlay={overlay}
                key={overlay.id}
            />
            )

        
    })
    }
    
    {/* {(() => {
        useEffect(() => {
            overlays.map((overlay) => {
                    console.log(overlay.header)
                    return( <OverlayIItem
                    deleteOverlay={deleteOverlay}
                    overlay={overlay}
                    key={1}
                />)
                
            })
            
            
        } , [overlays])
                            
    })()}    
      
     */}
    
        
    </div>

    )
}
export default Notify

