import { useEffect, useState, useRef } from "react";
import Typography from '@material-ui/core/Typography';
import React, { Component }  from 'react';

import {useOverlayManager} from './useOverlayManager';



  export default (props) => {
    const { overlay } = props;
    const { id, header, content, icon, iconColor, bgColor ,perma , buttons } = overlay;
    const [Header ,setHeader ] = useState(`${header}`)
    const [Description ,setDescription ] = useState(`${content}`)
    const [showIcons ,setIcons ] = useState(false)
    const [showCancel ,setCancel ] = useState(false)

    const [callNotify , setCallNotify] = useState(false)
    const [alive, setAlive] = useState(true);
    const [fadeOut, setFadeOut] = useState(false);

    const { addOverlay, deleteOverlay, overlays } = useOverlayManager();

    
    useEffect(() => {
      if(buttons === true) {
        setIcons(true)
      }
      if(content === 'Incoming Call!') {
        setCallNotify(true)
      }
      if(content === 'Calling...') {
        setCallNotify(true)
      }
      window.addEventListener("message", function (e) {
        if(e.data.type === 'Sheesh') {
          if(e.data.id === id) {
            if(e.data.header !== null && e.data.header === true) {
              setHeader(e.data.value)
            } else {
              let value = e.data.value
              if(e.data.callNotify === true) {
                var CallTime = e.data.value;
                var date = new Date(null);
           
                date.setSeconds(CallTime);
                let min = String(date.getMinutes())
                let sec =  String(date.getSeconds())
                if(min.length < 2) {
                    min = '0' + date.getMinutes()
                }
                if(sec.length < 2) {
                    sec = '0' + date.getSeconds()
                }

                value = `${min}:${sec}`
                setIcons(false)
                setCancel(true)
              }
              
              setDescription(value)
            }
          }
        
        } else if(e.data.type === 'closeNotify') {
          if(e.data.id === id) {
            if(callNotify !== true) {
              setFadeOut(true)
              setTimeout(() => {
                deleteOverlay(e.data.id)
            }, 500)
            }
         
          }
        }
      })
    }, []);

  
    useEffect(() => {
      const timer = setTimeout(() => setAlive(false), 2000);
      return () => {
        clearTimeout(timer);
      };
    }, []);
  
    useEffect(() => {
      if (!alive) {
        if(perma === true) {

        } else if(callNotify === true) {
          
        } else if(!perma || perma === false || callNotify === false || !callNotify) {
          setFadeOut(true)
          setTimeout(() => {
              deleteOverlay(id);
              //setFadeOut(false)
          }, 1500)
        }
       
      }
    }, [alive, deleteOverlay, id]);

    const handleClick = (id) => {
      if(perma === true) {

      } else {
        setFadeOut(true)
        setTimeout(() => {
            deleteOverlay(id)
            //setFadeOut(false)
        }, 500)
      }
    }

    const ConfirmButton = (id , user) => {
      if(callNotify === true) {
        fetch("https://ethicalpixel-phone/CallConfirmed", {
          method: "POST" , 
          body: JSON.stringify({callid: id , callerid: user , time: '321'}),
          headers: {
          "Content-Type": "application/json"
          }
        })
      } else {
        fetch("https://ethicalpixel-phone/ButtonInteraction", {
          method: "POST" , 
          body: JSON.stringify({ButtonID: id , Action: true}),
          headers: {
          "Content-Type": "application/json"
          }
        })
      }
       
      
    
      
    }


    const CancelButton = (id ) => {
      if(callNotify === true) {
        fetch("https://ethicalpixel-phone/HangoutCall", {
          method: "POST" , 
          body: JSON.stringify({callid: id}),
          headers: {
          "Content-Type": "application/json"
          }
        })
      } else {
        fetch("https://ethicalpixel-phone/ButtonInteraction", {
          method: "POST" , 
          body: JSON.stringify({ButtonID: id , Action: false}),
          headers: {
          "Content-Type": "application/json"
          }
        })
      }
       
      
    
      
    }



    return (
        <div id={id} className={fadeOut ? "notification-container notification-container-fade-out" : "notification-container "} onClick={() => handleClick(id)}>
        <div className="app-bar">
        <div className="icon" style={{background: bgColor, color: iconColor}}>
        <i className={`${icon}`} style={{WebkitTextStrokeColor: 'black', WebkitTextStrokeWidth: '0.3px' , padding: '0.3vh'}}></i>
        </div>
        <div className="name">
        <Typography style={{color: 'white', wordBreak: 'break-word'}} variant="body2" gutterBottom>{Header}</Typography>
        </div>
        <Typography style={{color: 'white', wordBreak: 'break-word'}} variant="body2" gutterBottom>just now</Typography>
        </div>
        <div className="content">
        <div className="text">
        <Typography style={{color: 'white', wordBreak: 'break-word'}} variant="body2" gutterBottom>{Description}</Typography>
        </div>
          {showIcons ?        
            <div className="actions"> 
             <i class="fas fa-check-circle" onClick={() => { ConfirmButton(id ,Header) }} style={{marginRight: '0.5vh' , color:'#bce086'}}></i> <i style={{color: '#d4983f'}} onClick={() => { CancelButton(id) }} class="fas fa-times-circle"></i> 
          </div>
        : ''}
         {showCancel ?        
            <div className="actions"> 
              <i onClick={() => { CancelButton(id) }} style={{marginRight: '0.5vh' , color: '#d4983f'}} class="fas fa-times-circle"></i> 
          </div>
        : ''}
        </div>
        </div>
    );
  };