
import React, { useState} from 'react';
import { showNotify } from './components/Notify'
import useOverlayManager from './components/useOverlayManager';
import OverlayIItem from './components/OverlayIItem';

export default actions => e =>
  [e.data].flat().map(data => handleMessage(actions, data))



  function handleMessage(actions, data) {
    switch (data.type) {
      case 'open':
        actions.showUI(true)
        break
      case 'setBankBalance':
        actions.setBankBalance(data.balance)
        break
      case 'addNotify':
        actions.addShowNotify(data)
        break
      case 'UpdateSMS':
        actions.updateMessage(data.messages)
        break
    
     
      case 'getContactsData':
        actions.setContactsData(data.data)
        break
      
      case 'addToHUD':
        const { hudType, amount, message } = data
        actions.addToHUD({
          type: hudType,
          amount: amount,
          message: message,
        })
        break
      default:
    }
}
