import history from './history'
import React from "react"; 
import { useAppActions, useAppState } from './context'
import { updateNotifyText } from './components/useOverlayManager'

// import { useOverlayManager } from './components/useOverlayManager'

// const { addOverlay, deleteOverlay, overlays } = useOverlayManager();

export const IsDev = process.env.NODE_ENV !== 'production'

export const initialState = {
  shown: IsDev,
  showUI: false,
  NotifySmall: false,
  overlays: [],
  OverlayId: 0,
  UpdatedMessages: [],
  Favorites: [],

}

export const actions = {
  
  showUI: (draft, shown) => {
    draft.showUI = !draft.showUI
  },
  setContactsData: (draft, data) => {
    draft.contactsData = data
  },
  setFavorites: (draft, data) => {
    draft.Favorites = data
  },
  setNotifyShow: (draft, data) => {

    draft.NotifySmall = !draft.NotifySmall
  },
  setOverlays: (draft, data) => {
    let b = draft.overlays
    for(var i = 0; i < b.length; i++) {
      if(b[i].id == data) {
          b.splice(i, 1);
          break;
      }
    }
  },

  updateMessage: (draft , data) => {
    draft.UpdatedMessages = data.messages
  },
  addShowNotify: (draft , data) => {
    let id = draft.overlays.length+1
    if(data.perma === true) {
      id = data.id
    }
    draft.overlays.push({ id: id, header: data.title, content: data.description, icon: data.icon, iconColor: "#fff", bgColor: data.background , perma: data.perma , buttons: data.buttons  , callnotif: data.callNotify})
  },

  
}
